import { createSlice, CaseReducer, PayloadAction } from "@reduxjs/toolkit";
import { Data } from "../../../pages/admin/Profiles/types";

export type actionParam = {
  id: number;
  description: string;
  params: any[];
};

export type profilesPageState = {
  profilesList: Data[];
  rerender: boolean;
  profileName: string;
  profileActions: actionParam[];
};

const initialState = {
  profilesList: [],
  rerender: false,
  profileName: "",
  profileActions: [],
};

export type profilesPageActions = {
  updateList: CaseReducer<profilesPageState, PayloadAction<Data[]>>;
  requestRerender: CaseReducer<
    profilesPageState,
    PayloadAction<profilesPageState>
  >;
  deleteProfile: CaseReducer<profilesPageState, PayloadAction<number>>;
  //   editAction: CaseReducer<actionsPageState, PayloadAction<Data>>;
  updateProfileName: CaseReducer<profilesPageState, PayloadAction<string>>;
  updateProfileActions: CaseReducer<
    profilesPageState,
    PayloadAction<actionParam[]>
  >;
  updateProfileActionParams: CaseReducer<
    profilesPageState,
    PayloadAction<actionParam>
  >;
};

const profilesPageSlice = createSlice<profilesPageState, profilesPageActions>({
  name: "profilePage",
  initialState,
  reducers: {
    updateList: (state, action) => {
      return { ...state, profilesList: action.payload };
    },
    requestRerender: (state, _action) => {
      return { ...state, rerender: !state.rerender };
    },
    deleteProfile: (state, action) => {
      return {
        ...state,
        profilesList: state.profilesList.filter(
          (elm: Data) => elm.id !== action.payload
        ),
      };
    },
    updateProfileName: (state, action) => {
      return {
        ...state,
        profileName: action.payload,
      };
    },
    updateProfileActions: (state, action) => {
      return {
        ...state,
        profileActions: action.payload,
      };
    },
    updateProfileActionParams: (state, action) => {
      return {
        ...state,
      };
    },
    // editAction: (state, action) => {
    //   let index = state.actionsList.findIndex(
    //     (x) => x.id === action.payload.id
    //   );
    //   let actionsList = [...state.actionsList];
    //   actionsList[index] = action.payload;
    //   return {
    //     ...state,
    //     actionsList,
    //   };
    // },
  },
});

export default profilesPageSlice;
