import React from "react";
import { connect } from "react-redux";

import { Helmet } from "react-helmet";

import DateFnsUtils from "@date-io/date-fns";
import { ThemeProvider as MuiThemeProvider } from "@material-ui/core/styles";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import { StylesProvider } from "@material-ui/styles";
import { ThemeProvider } from "styled-components";
import { RootState } from "./redux/reducers";
import { useSelector } from "react-redux";

import maTheme from "./theme";
import Routes from "./routes/Routes";
const selector = (state: RootState) => {
  return state.theme;
};

function App() {
  const theme = useSelector(selector);

  return (
    <React.Fragment>
      <Helmet
        titleTemplate="%s | Video Robot"
        // defaultTitle="Material App - React Admin & Dashboard Template"
      />
      <StylesProvider injectFirst>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <MuiThemeProvider theme={maTheme[theme]}>
            <ThemeProvider theme={maTheme[theme]}>
              <Routes />
            </ThemeProvider>
          </MuiThemeProvider>
        </MuiPickersUtilsProvider>
      </StylesProvider>
    </React.Fragment>
  );
}
// @ts-ignore
export default connect((store) => ({ theme: store.themeReducer }))(App);
