const selector = (state: RootState) => {
  return state.SideBar;
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    hide: {
      display: "none",
    },
    drawer: {
      width: 260,
      // flexShrink: 0,
      whiteSpace: "nowrap",
      overflowX: "hidden",
    },
    drawerOpen: {
      width: 260,
      overflowX: "hidden",
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    drawerClose: {
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      overflowX: "hidden",
      width: theme.spacing(14) + 1,
      // [theme.breakpoints.up("sm")]: {
      //   width: theme.spacing(14) + 1,
      // },
    },
  })
);

type SidebarCategoryProps = {
  name?: string;
  icon: any;
  to?: any;
  classes?: any;
  isOpen?: boolean;
  isCollapsable: boolean;
  badge?: any;
  button?: any;
  onClick?: () => void;
  activeClassName?: any;
  component?: any;
  exact?: any;
  open?: boolean;
};
type SideBarLinkProps = {
  name?: string;
  to?: any;
  badge: any;
  icon?: any;
  component?: any;
  dense?: any;
  button?: any;
  activeClassName?: any;
};

type SideBarProps = {
  routes?: any;

  classes?: any;
  staticContext?: any;
  location: any;
};
function SidebarCategory({
  open,
  name,
  icon,
  classes,
  isOpen,
  isCollapsable,
  badge,
  ...rest
}: SidebarCategoryProps) {
  return (
    <Category {...rest}>
      {icon}
      {<CategoryText>{name}</CategoryText>}
      {open && isCollapsable ? (
        isOpen ? (
          <CategoryIconMore />
        ) : (
          <CategoryIconLess />
        )
      ) : null}
      {/* {open && badge ? <CategoryBadge label={badge} /> : ""} */}
    </Category>
  );
}

function SidebarLink({ name, to, badge }: SideBarLinkProps) {
  return (
    <Link
      button
      dense
      component={NavLink}
      exact
      to={to}
      activeClassName="active"
    >
      <LinkText>{name}</LinkText>
      {/* {badge ? <LinkBadge label={badge} /> : ""} */}
    </Link>
  );
}

function Sidebar({ classes, staticContext, location, ...rest }: SideBarProps) {
  const initOpenRoutes = () => {
    /* Open collapse element that matches current url */
    const pathName = location.pathname;

    let _routes = {};

    routes.forEach((route: any, index: any) => {
      const isActive = pathName.indexOf(route.path) === 0;
      const isOpen = route.open;
      const isHome = route.containsHome && pathName === "/" ? true : false;

      _routes = Object.assign({}, _routes, {
        [index]: isActive || isOpen || isHome,
      });
    });

    return _routes;
  };

  const [openRoutes, setOpenRoutes] = useState<any>(() => initOpenRoutes());

  const toggle = (index: any) => {
    // Collapse all elements
    Object.keys(openRoutes).forEach((item) => {
      // console.log(openRoutes);

      openRoutes[index] ||
        setOpenRoutes((openRoutes: any) =>
          Object.assign({}, openRoutes, { [item]: false })
        );
    });

    // Toggle selected element
    setOpenRoutes((openRoutes: any) =>
      Object.assign({}, openRoutes, { [index]: !openRoutes[index] })
    );
  };
  const myClasses = useStyles();
  const { open } = useSelector(selector);
  const dispatch = useDispatch();
  // const [open, setOpen] = React.useState(true);

  const handleDrawerOpen = () => {
    // setOpen(true);
    if (open) return;
    dispatch(setOpen());
  };

  const handleDrawerClose = () => {
    // setOpen(false);
    if (!open) return;
    dispatch(setOpen());
  };

  return (
    <Drawer
      variant="permanent"
      {...rest}
      className={clsx(myClasses.drawer, {
        [myClasses.drawerOpen]: open,
        [myClasses.drawerClose]: !open,
      })}
      classes={{
        paper: clsx({
          [myClasses.drawerOpen]: open,
          [myClasses.drawerClose]: !open,
        }),
      }}
      onMouseOver={handleDrawerOpen}
      onMouseLeave={handleDrawerClose}
      // open={hover}
    >
      <Brand>
        <img src={Logo} alt="" style={{ width: "22px", height: "22px" }} />{" "}
        <Box ml={4}>Video Robot</Box>
      </Brand>
      <Scrollbar>
        <List disablePadding>
          <Items>
            {routes.map((category: any, index: number) => (
              <React.Fragment key={index}>
                {/* {category.header ? (
                  <SidebarSection>{category.header}</SidebarSection>
                ) : null} */}

                {open && category.children ? (
                  <React.Fragment key={index}>
                    <SidebarCategory
                      open={open}
                      isOpen={!openRoutes[index]}
                      isCollapsable={true}
                      name={category.id}
                      icon={category.icon}
                      button={true}
                      onClick={() => toggle(index)}
                    />

                    <Collapse
                      in={openRoutes[index]}
                      timeout="auto"
                      unmountOnExit
                    >
                      {category.children.map((route: any, index: any) => (
                        <SidebarLink
                          key={index}
                          name={route.name}
                          to={route.path}
                          icon={route.icon}
                          badge={route.badge}
                        />
                      ))}
                    </Collapse>
                  </React.Fragment>
                ) : (
                  <SidebarCategory
                    open={open}
                    isCollapsable={false}
                    name={category.id}
                    to={category.path}
                    activeClassName="active"
                    component={NavLink}
                    icon={category.icon}
                    exact
                    badge={category.badge}
                    onClick={() => toggle(index)}
                  />
                )}
              </React.Fragment>
            ))}
          </Items>
        </List>
      </Scrollbar>
    </Drawer>
  );
}

export default withRouter(Sidebar);
