import React from "react";
import styled from "styled-components";

import { CircularProgress } from "@material-ui/core";

const Root = styled.div`
  justify-content: center;
  align-items: center;
  display: flex;
  min-height: 100%;
`;
type CircularProgressProps = {
  m: number;
  color: "secondary" | "inherit" | "primary" | undefined;
};
function Loader() {
  const CircularProgressProp: CircularProgressProps = {
    m: 2,
    color: "secondary",
  };
  return (
    <Root>
      <CircularProgress {...CircularProgressProp} />
    </Root>
  );
}

export default Loader;
