import { AppThunk } from "../../store";

import profilesPageSlice, { actionParam } from "./slice";
import { Data } from "../../../pages/admin/Profiles/types";

export const updateList = (list: Data[]): AppThunk => (dispatch) => {
  dispatch(profilesPageSlice.actions.updateList(list));
};

export const requestRerender = (): AppThunk => (dispatch, getState) => {
  const { profilesPage } = getState();
  dispatch(profilesPageSlice.actions.requestRerender(profilesPage));
};

export const deleteProfile = (id: number): AppThunk => (dispatch, getState) => {
  dispatch(profilesPageSlice.actions.deleteProfile(id));
};

export const updateProfileName = (name: string): AppThunk => (
  dispatch,
  getState
) => {
  dispatch(profilesPageSlice.actions.updateProfileName(name));
};

export const updateProfileActions = (actions: actionParam[]): AppThunk => (
  dispatch,
  getState
) => {
  dispatch(profilesPageSlice.actions.updateProfileActions(actions));
};

