import { createSlice, CaseReducer, PayloadAction } from "@reduxjs/toolkit";
import { Data } from "../../../pages/admin/Actions/types";
export type actionsPageState = {
  actionsList: Data[];
  searchResult: Data[];
  rerender: boolean;
  openModal: boolean;
};

const initialState = {
  actionsList: [],
  searchResult: [],
  rerender: false,
  openModal: false,
};

export type actionsPageActions = {
  updateList: CaseReducer<actionsPageState, PayloadAction<Data[]>>;
  updateSearchList: CaseReducer<actionsPageState, PayloadAction<Data[]>>;
  requestRerender: CaseReducer<
    actionsPageState,
    PayloadAction<actionsPageState>
  >;
  deleteAction: CaseReducer<actionsPageState, PayloadAction<number>>;
  editAction: CaseReducer<actionsPageState, PayloadAction<Data>>;
  openModalAction: CaseReducer<
    actionsPageState,
    PayloadAction<actionsPageState>
  >;
};

const actionsPageSlice = createSlice<actionsPageState, actionsPageActions>({
  name: "actionsPage",
  initialState,
  reducers: {
    updateList: (state, action) => {
      return { ...state, actionsList: action.payload };
    },
    updateSearchList: (state, action) => {
      return { ...state, searchResult: action.payload };
    },
    requestRerender: (state, _action) => {
      return { ...state, rerender: !state.rerender };
    },
    deleteAction: (state, action) => {
      return {
        ...state,
        actionsList: state.actionsList.filter(
          (elm: Data) => elm.id !== action.payload
        ),
        searchResult: state.actionsList.filter(
          (elm: Data) => elm.id !== action.payload
        ),
      };
    },
    editAction: (state, action) => {
      let index = state.actionsList.findIndex(
        (x) => x.id === action.payload.id
      );
      let actionsList = [...state.actionsList];
      let searchResult = [...state.actionsList];
      actionsList[index] = action.payload;
      return {
        ...state,
        actionsList,
        searchResult,
      };
    },
    openModalAction: (state, _action) => {
      return { ...state, openModal: !state.openModal };
    },
  },
});

export default actionsPageSlice;
