import { AppThunk } from "../../store";

import actionsPageSlice from "./slice";
import { Data } from "../../../pages/admin/Actions/types";

export const updateList = (list: Data[]): AppThunk => (dispatch) => {
  dispatch(actionsPageSlice.actions.updateList(list));
};
export const updateSearchList = (list: Data[]): AppThunk => (dispatch) => {
  dispatch(actionsPageSlice.actions.updateSearchList(list));
};
export const requestRerender = (): AppThunk => (dispatch, getState) => {
  const { actionsPage } = getState();
  dispatch(actionsPageSlice.actions.requestRerender(actionsPage));
};

export const deleteAction = (id: number): AppThunk => (dispatch, getState) => {
  dispatch(actionsPageSlice.actions.deleteAction(id));
};

export const editAction = (action: Data): AppThunk => (dispatch) => {
  dispatch(actionsPageSlice.actions.editAction(action));
};

export const openModalAction = (): AppThunk => (dispatch, getState) => {
  const { actionsPage } = getState();
  dispatch(actionsPageSlice.actions.openModalAction(actionsPage));
};
