import { createSlice, CaseReducer, PayloadAction } from "@reduxjs/toolkit";

export type themeState = 0 | 1 | 2 | 3 | 4 | 5;

export type themeActions = {
  setTheme: CaseReducer<themeState, PayloadAction<themeState>>;
};

const themeSlice = createSlice<themeState, themeActions>({
  name: "theme",
  initialState: 0,
  reducers: {
    setTheme: (_state, action) => action.payload,
  },
});

export default themeSlice;
